import React, { Dispatch, SetStateAction } from 'react';
import { Toast } from 'react-bootstrap';
import styles from '../../pages/Edit/styles.module.scss';
import CheckMark from '../../assets/CheckMark.component.svg';
import CloseIcon from '../../assets/CloseIcon.component.svg';

interface IAlertProps {
  show: boolean,
  onClose: Dispatch<SetStateAction<boolean>>,
  alertMessage: string,
}

const Alert = ({ show, onClose, alertMessage }): React.ReactElement => {
  return (
    <Toast
      show={show}
      className={styles['success-alert-message']}
      delay={3000}
      onClose={() => onClose(false)}
      autohide
      animation
    >
      <Toast.Body>
        <div>
          <CheckMark />
          <span>
            {`Успех! ${alertMessage}`}
          </span>
        </div>
        <button type="button" onClick={() => onClose(false)}>
          <CloseIcon />
        </button>
      </Toast.Body>
    </Toast>
  );
};

export default Alert;
